<template>
  <div class="hello">
    <p>输入框12</p>
    <input ref="cardInput" v-model="cardNoFormat">
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data() {
    return {
      cardNo: '',
    };
  },
  computed: {
    cardNoFormat: {
      get() {
        return this.cardNo;
      },
      set(v) {
        setTimeout(() => {
          this.cardNo = v.replace(/(\d{4})(?=\d)/g, '$1 ');
        }, 1);
      },
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



